@font-face {
  font-family: 'Racing Sans One';
  font-style: normal;
  font-display: swap;
  src: local('Racing Sans One'), local('RacingSansOne-Regular'), url(./RacingSansOne-Regular.woff2) format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #264b61;
  font-family: Roboto;
}

.HeaderBox {
  width: 100%;
  background: #04151F;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 5px 5px 5px rgba(0,0,0,0.75);
  user-select: none;
}

.TitleBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 9em;
  font-family: 'Racing Sans One';
  color: #e3cc22;
  margin-top: -30px;
  margin-bottom: 5px;
}

.SubTitleBox {
  font-size: 1rem;
  font-style: italic;
  font-family: 'Roboto';
  color: whitesmoke;
  margin-top: -25px;
}

.TabBox{
  display: flex;
  flex-direction: row;
}

.Tab {
  display: flex;
  align-items: center;
  justify-content: center;
  color: whitesmoke;
  font-size: 1.1em;
  background: #062436;
  margin: 5px;
  width: 5vw;
  height: 2vw;
}

.Tab:hover {
  cursor: pointer;
}

.Content {
  display: block;
  text-align: center;
  overflow-y: auto;
  padding-top: 10px;
}

.CarTile {
  display: inline-block;
  width: 30vw;
  height: 10vw;
  background: white;
  margin: 10px;
  box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.20);
}

.CarTileContent {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

.CarTileImage {
  height: 96%;
  width: 33%;
  margin: 3px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background: whitesmoke;
}

.CarTileText {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: Roboto;
  text-align: left;
  padding-left: 5px;
}

.CarTileTitle {
  color: #04151F;
  font-size: 1.5em;
  font-family: 'Racing Sans One', cursive;
}